@font-face {
	font-family: 'smartadmin-icons';
	src: url('../../../webfonts/smartadmin/fonts/smartadmin-icons.eot');
	src: url('../../../webfonts/smartadmin/fonts/smartadmin-icons.eot?#iefix') format('embedded-opentype'),
		 url('../../../webfonts/smartadmin/fonts/smartadmin-icons.woff2') format('woff2'),
		 url('../../../webfonts/smartadmin/fonts/smartadmin-icons.woff') format('woff'),
		 url('../../../webfonts/smartadmin/fonts/smartadmin-icons.ttf') format('truetype'),
		 url('../../../webfonts/smartadmin/fonts/smartadmin-icons.svg#nextgen-icons') format('svg');
	font-weight: normal;
	font-style: normal;
}

/* Use the following CSS code if you want to have a class per icon */
.#{$cust-icon-prefix},
[data-icon]:before,
[class^="sa-base-"],
[class*=" sa-base-"] {
	font-family: 'smartadmin-icons';
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
	//vertical-align: middle;
}


.navbar-toggler {
	.#{$cust-icon-prefix}:before,
	[data-icon]:before {
		vertical-align: inherit !important;
	}
}

